import * as Styles from './Card.styles'

import { useNavigate } from "react-router-dom";

const Card = (props: any) => {
  const navigate = useNavigate();

  var cardsDeb = props.items

  return (
    <Styles.CardContainer>
      <Styles.CardContent>
        {
          cardsDeb.map((el: any, i: number) =>
            <Styles.CardObject key={i} onClick={()=> navigate(el.path ? el.path : `/info-page/${el.id}`)}> 
              <div className='img-card'>
                <img src={el.img} />
              </div>
              { el.data && <span className='desc'>{el.data}</span> }
              <span className='title'>{el.title}</span>
              { el.desc && <span className='desc'>{el.desc}</span> }
              { el.padre && <span className='desc'>{el.padre}</span> }
            </Styles.CardObject>
          )
        }
      </Styles.CardContent>
    </Styles.CardContainer>
  )
}

export default Card