import { useNavigate } from 'react-router-dom';
import * as Styles from './Formulario.styles'

const Page = () =>{
  const navigate = useNavigate();

    return (
      <Styles.MainContainer>
          <button className='voltarButton' onClick={() => navigate(-1)}><span className="material-symbols-outlined">chevron_left</span><span>Voltar</span></button>
          <Styles.FormContainer action="/">
            <div className="title">
              <h1>Adicionar Novo Evento</h1>
              <h2>Casamento</h2>
            </div>
            <div className='formMain'>

              <div className='nomes displayFlex'>
                <div className='inputNomes flexColumn'>
                  <span>Pretendente 01</span>
                  <input type={'text'} />
                </div>
                <div className='inputNomes flexColumn'>
                  <span>Pretendente 02</span>
                  <input type={'text'} />
                </div>
              </div>

              <div className='telefoneContato flexColumn'>
                <span>Telefone para contato</span>
                <input type={'text'} />
              </div>

              <div className='telefoneContato flexColumn'>
                <span>Whatsapp</span>
                <input type={'text'} />
              </div>

              <div className='observacoes flexColumn'>
                <span>Observações</span>
                <textarea/>
              </div>

              <div className='selections'>

                <div className='selectionarPadre flexColumn'>
                  <label>Padre:</label>
                  <select>
                    <option value={"PadreJoao"}>Padre João Bosco</option>
                  </select>
                </div>

                {/* <div className='selectionarSonoplasta flexColumn'>
                  <label>Sonoplasta:</label>
                  <select>
                    <option value={"PadreJoao"}>Padre João Bosco</option>
                  </select>
                </div> */}

              </div>

              <div className='buttonSend'>
                <button className='active'>Enviar Dados</button>
              </div>

            </div>
          </Styles.FormContainer>
      </Styles.MainContainer>
    );
}

export default Page

