import styled from 'styled-components';

export const MainContainer = styled.div`
    .adicionarNovo{
        padding: 20px;
        border: 1px solid rgba(255,255,255,0.1);
        padding-top: 15px;
        padding-bottom: 15px;
        cursor: pointer;
        text-align: left;
        background: rgba(255,255,255,0.05);
        border-radius: 8px;
        margin-bottom: 30px;

        display: flex;
        align-items: center;
        gap: 10px;

        transition: .3s background;

        :hover{
            background: rgba(255,255,255,0.1);
        }
    }
`;