import styled from 'styled-components';

export const CardContainer = styled.div`
  width: 100%;
`;

export const CardContent = styled.div`
  display: flex;
  justify-content: left;
  gap: 32px;
`;

export const CardObject = styled.div`
  color: white;
  min-width: 230px;
  width: 230px;
  min-height: 330px;
  border-radius: 8px;
  overflow: hidden;
  background: rgba(255,255,255,0.04);
  position: relative;
  border: 1px solid rgba(255,255,255,0.15);
  box-shadow: 0px 10px 10px -3px rgba(0,0,0,0.18);
  cursor: pointer;
  transition: .2s;

  display: flex;
  flex-direction: column;

  :hover{
    transform: translateY(-3px);

    /* img{
      transform: scale(1.05);
    } */
  }

  :active{
    transform: scale(1.0);
  }

  span{
    position: relative;
    left: 16px;
    top: 12px;
    font-weight: 600;
  }

  .title{
    font-size: 20px;
  }

  .desc{
    font-size: 14px;
    margin-top: 4px;
    opacity: 0.5;
    font-weight: 500;
    line-height: 17px;
  }

  .padre{
    font-size: 12px;
  }

  .img-card{
    height: 70%;
    position: relative;
    overflow: hidden;

    /* ::after{
      display: block;
      content: '';
      opacity: 1;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;

      background: linear-gradient(0deg, rgba(255,255,255,1) 20%, rgba(0,212,255,0) 100%);
    } */
  }

  img{
    height: 100%;
    width: 100%;

    transition: .1s;
    transition-property: transform;

    object-fit: cover;
  }
`;
