import * as Styles from './Casamento.styles'
import Card from '../../Components/Cards/Card'
import { useNavigate } from 'react-router-dom'

const cards = [
  {
    "id": 1,
    "img": "https://www.inesquecivelcasamento.com.br/wp-content/uploads/2022/06/Casamento-Luana-e-Bernardo-por-Jannu-previa-73.jpg",
    "title": "Maria & João",
    "data": "20 de Março",
    "padre": "Pe. João Bosco"
  }
]

const Page = () =>{
    const navigate = useNavigate();

    return (
      <Styles.MainContainer>
          <button className='adicionarNovo' onClick={() => navigate('/adicionar-casamento')}><span className="material-symbols-outlined">add</span><span>Adicionar Novo Horário de Casamento</span></button>
          <Card items={cards} />
      </Styles.MainContainer>
    );
}

export default Page